function Note(props) {

  const deleteNote = () =>{
    props.deleteItem(props.id)
  }


  return (
    <div className ='note-container'>
   <div className='container'>
   <div className="Note">
   <h1>{props.title}</h1>
   
   <p>{props.content}</p>
       <button className='trash'>
      <i className='fas fa-trash-alt' onClick = {deleteNote}></i>
       </button>
   </div>
   </div>

   </div>
  );
}

export default Note;
